import Vue from 'vue'
import Router from 'vue-router'

var d = new Date();
var todaydate=d.getDate();
// console.log("today date====>",todaydate);

    if(todaydate>window.localStorage.getItem("loginUserDate") || window.localStorage.getItem("loginUserDate")==null){
      window.localStorage.clear();
    }


// Containers
const DefaultContainer = () => import('@/containers/DefaultContainer')

// Views
const Dashboard = () => import('@/views/toutPaie/Dashboard')

// Views - Pages
const Login = () => import('@/views/pages/Login')
const changepassword = () => import('@/views/pages/change-password')
const changepasswordsuccessfully = () => import('@/views/pages/changepassword-successfully')
const Managers = () => import('@/views/toutPaie/Managers')
const Stores = () => import('@/views/toutPaie/Stores')
const ProductGroups = () => import('@/views/toutPaie/Product-Groups')
const Products = () => import('@/views/toutPaie/Products')
const Ads = () => import('@/views/toutPaie/Ads')
const Posts = () => import('@/views/toutPaie/Posts')
const Orders = () => import('@/views/toutPaie/Orders')
const Users = () => import('@/views/toutPaie/Users')
const Payment = () => import('@/views/toutPaie/Payment')
const StoreImage = () => import('@/views/toutPaie/Store-Image')
const ForgotPassword = () => import('@/views/toutPaie/Forgot-Password')
const ResetPassword = () => import('@/views/toutPaie/Reset-Password')

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/managers',
      name: 'Home',
      component: DefaultContainer,
      children: [
        {
          path: '/dashboard',
          name: "Dashboard",
          component: Dashboard,
        },
        {
            path: '/stores',
            name : 'Stores',
            component: Stores,
            meta: { visibility : ["SA",,"SM","M"] }
        },
        {
            path: '/managers',
            name : 'Managers',
            component: Managers,
            meta: { visibility : ["SA"] }
        },
        {
          path: '/product-groups',
          name : 'Product Groups',
          component: ProductGroups,
          meta: { visibility : ["SA","SM","M"] }
        },
        {
          path: '/products',
          name : 'Products',
          component: Products,
          meta: { visibility : ["SA","SM","M"] }
        },
        {
          path: '/ads',
          name : 'Ads',
          component: Ads,
          meta: { visibility : ["SA","SM"] }
        },
        {
          path: '/posts',
          name : 'Posts',
          component: Posts,
          meta: { visibility : ["SA"] }
        },
        {
          path: '/orders',
          name : 'Orders',
          component: Orders,
          meta: { visibility : ["SA","SM","M"] }
        },
        {
          path: '/users',
          name : 'Application_Users',
          component: Users,
          meta: { visibility : ["SA"] }
        },
        {
          path: '/payment',
          name : 'Payment',
          component: Payment,
          meta: { visibility : ["SA","SM","M"] }
        },
        {
          path: '/store-image',
          name : 'Store_Advertisement',
          component: StoreImage,
          meta: { visibility : ["SA"] }
        },
        
      ]
    },
    {
      path: '/forgot-password',
      name: 'Forgot Password',
      component: ForgotPassword
    },
    {
      path: '/reset-password',
      name: 'Reset Password',
      component: ResetPassword
    },
    {
      path: '/pages',
      redirect: '/pages/login',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '/login',
          name: 'Login',
          component: Login
        }
        
      ]
    },


    {
      path: '/pages',
      redirect: '/pages/change-password',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '/change-password',
          name: 'change password',
          component: changepassword
        }
      ]
    },
    
    {
      path: '/pages/changepassword-successfully',
      redirect: '/pages/changepassword-successfully',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '/changepassword-successfully',
          name: 'change password',
          component: changepasswordsuccessfully
        }
      ]
    }

  ]
})

router.beforeEach((to, from, next) => {
   
  // if(to.path == '/change-password'){
  //   router.push('/reset-password');
  //   return;
  // }



  if(to.path == '/changepassword-successfully'){
    router.push('/changepassword-successfully');
  }

  if(to.path != '/login' && to.path != '/' && to.path != '/forgot-password' && !to.path.includes('/reset-password')) {
    const auth_token = window.localStorage.getItem('auth_token');
    var user = JSON.parse(window.localStorage.getItem('loginUser'));
    // console.log("xzddddddd",user);
    if(user && user.Role && auth_token) {
      if(to.meta.visibility){
        //console.log(to.meta.visibility);
        if(to.meta.visibility.includes(user.Role)){
          next();
        }else {
          next('/Dashboard');
        }
      } else {
        next();
      }
    } else if(to.path == '/change-password'){

    }
     else {
      next('/login');
    }
  }else{
    next();
  }
})

export default router
